import './App.css';
import React, { useState } from "react";
import { Component } from "react";
import { Link, Route, Switch } from 'react-router-dom'
import { Navbar, Nav, Container } from "react-bootstrap";
import HomePage from "./pages/HomePage";
import JotForm from "./pages/JotForm";
import Cards from "./pages/Cards";
import NewUser from "./pages/NewUser";
import Contacts from "./pages/Contacts";
import 'fontsource-roboto';
import { Image } from  "react-bootstrap";

function App(props) {
    //const [tasks, setTasks] = useState(props.isLoggedIn);
    console.log("App.js "+props.isLoggedIn);
    //alert("App.js "+props.isLoggedIn);
    function NavChangeColor(page) {
        let div = document.getElementById("navbar")
        if (page === "Contacts") {
            div.style.background = "#343a40";
            //div.style.color = "#3aaa35";
        }
        else {
            div.style.background = "#3aaa35";
        }
    }
    if (props.isLoggedIn) {
        console.log("True "+props.isLoggedIn);
        return (
            <Container>
                <header className="header">
                    <Container id={props}>
                        <Navbar id="navbar" variant="dark" className="navbar_border-radius color-nav" >
                            <Navbar.Brand href="/">
                                <Image src="ET_logo.png" width="25" height="25" className="d-inline-block align-top" />
                                {/*<img*/}
                                {/*    src="ET_logo.png"*/}
                                {/*    width="30"*/}
                                {/*    height="30"*/}
                                {/*    className="d-inline-block align-top"*/}
                                {/*    alt="Mijn portal"*/}
                                {/*/>*/}
                                {' '}Mijn portal
                            </Navbar.Brand>
                            <Nav className="mr-auto">
                                {/*<Nav.Link href="/">Home</Nav.Link>*/}
                                <Nav.Link as={Link} to="/new" onClick={() => NavChangeColor('New')}>Nieuwe aanvraag</Nav.Link>
                                <Nav.Link as={Link} to="/check" onClick={() => NavChangeColor('Check')}>Status mijn dossiers</Nav.Link>
                                {/*<Nav.Link as={Link} to="/contacts" onClick={() => NavChangeColor('Contacts')}>Contacten</Nav.Link>*/}
                            </Nav>
                        </Navbar>
                        <Image src="ET_banner.png" fluid />
                    </Container>
                </header>
                <Switch>
                    <Route exact path="/" component={HomePage} />
                    <Route path="/new" component={JotForm} />
                    <Route path="/check" component={Cards} />
                    <Route path="/new_user" component={NewUser} />
                    <Route path="/contacts" component={Contacts} />
                </Switch>
                <p className="mt-5 mb-3 text-muted footer">Electrotest &copy; 2021</p>
            </Container>
        );
    } else if (props.isLoggedIn === undefined) {
        console.log("False "+props.isLoggedIn);
        return (
            <Container>
                <header className="header">
                    <br />
                    <Container>
                        <Image src="ET_banner.png" thumbnail />
                    </Container>
                    <br />
                    <Container id={props}>
                        <Navbar id="navbar" variant="dark" className="navbar_border-radius color-nav" >
                            <Navbar.Brand href="/">
                                <Image src="ET_logo.png" width="25" height="25" className="d-inline-block align-top" />
                                {/*<img*/}
                                {/*    src="ET_logo.png"*/}
                                {/*    width="30"*/}
                                {/*    height="30"*/}
                                {/*    className="d-inline-block align-top"*/}
                                {/*    alt="Mijn portal"*/}
                                {/*/>*/}
                                {' '}Mijn portal
                            </Navbar.Brand>
                            <Nav className="mr-auto">
                                {/*<Nav.Link href="/">Home</Nav.Link>*/}
                                {/*<Nav.Link as={Link} to="/new" onClick={() => NavChangeColor('New')}>Nieuwe aanvraag</Nav.Link>*/}
                                {/*<Nav.Link as={Link} to="/check" onClick={() => NavChangeColor('Check')}>Status mijn dossiers</Nav.Link>*/}
                                <Nav.Link as={Link} to="/contacts" onClick={() => NavChangeColor('Contacts')}>Contacten</Nav.Link>
                            </Nav>
                        </Navbar>
                    </Container>
                </header>
                <br />
                <br />
                <Switch>
                    <Route exact path="/" component={HomePage} />
                    <Route path="/new" component={JotForm} />
                    <Route path="/check" component={Cards} />
                    <Route path="/new_user" component={NewUser} />
                    <Route path="/contacts" component={Contacts} />
                </Switch>
                <p className="mt-5 mb-3 text-muted footer">Electrotest &copy; 2021</p>
            </Container>
        );
    }

}

export default App;

